  .footer .bottom-bar .social li {
    float: left; }
  .footer-col-inner {
    margin-bottom: 15px; }
  .home-page .testimonials .carousel .item {
    min-height: inherit; }
  .home-page .events .section-content {
    min-height: inherit; }
  .footer .tweet {
    min-height: inherit; }
  .page-wrapper .album-cover {
    min-height: 360px; } }

/* Medium devices (desktops, 992px and up) */
@media (min-width: 992px) {
  .home-page .testimonials .carousel .item {
    min-height: 298px; }
  .home-page .recent .recent-iframe {
    height: 287px; }
  .footer .bottom-bar .social li {
    float: right; }
  .footer .tweet {
    min-height: 100px; }
  .page-wrapper .album-cover {
    min-height: 360px; }
  /* Sticky Footer */
  html,
  body {
    height: 100%; }
  body {
    font-size: 13px; }
  .wrapper {
    margin-bottom: -46px;
    min-height: 100%;
    /* equal to footer height */ }
  .wrapper:after {
    content: "";
    display: block;
    height: 46px;
    /* must be the same height as footer */ }
  .footer {
    height: 46px;
    /* must be the same height as footer */ } }

/* Large devices (large desktops, 1200px and up) */
@media (min-width: 1200px) {
  .home-page .course-finder .keywords input {
    width: 255px; } }

